.c-appdraw {
	position: fixed;
	bottom:0px;
	left:0px;
	right:0px;
}

.c-appdraw__item {
	align-items: center;
	position:relative;
	margin-bottom:20px;
	text-align: center;
	font-size: rem(12px);
}

.c-appdaw__icon {
	width:rem(60px);
	height:rem(60px);
	margin:0px auto;
	text-align: center;
	align-items: center;
	background-color:color('dingey-light');
	border-radius:10px;
	display:flex;
	position:relative;
	padding:5px;
	fill:#73896e;
}

.c-appdaw__icon:active {
	fill:white !important;
}

.c-appdraw__icon--active {
	fill:white !important;
}